import * as React from "react";
import { graphql, StaticQuery } from "gatsby";
import Layout from "partials/layouts/layout-page";
import Mdx from "components/mdx";

/**
 * QUery
 */
const query = graphql`
  query MyQuery {
    mdx(slug: { eq: "legal" }) {
      body
    }
  }
`;

/**
 * View
 */
const IndexPage = () => (
  <StaticQuery
    query={query}
    render={(data) => (
      <Layout meta={{ title: "Mentions lÃ©gales" }}>
        <div className="container my-5">
          <Mdx body={data.mdx.body} />
        </div>
      </Layout>
    )}
  />
)

export default IndexPage
